body {
  margin: 0;
  padding: 0;
  background: hsla(0, 0%, 100%, 1);
  font-size: 12pt;
  color: hsla(0, 0%, 20%, 1); }
body.index {
  background-color: hsla(220, 34%, 30%, 1);
  color: hsla(219, 100%, 95%, 1); }
ul, ol {
  padding: 0; }
li {
  list-style: none; }

// top
#header-wrapper {
  overflow: hidden;
  padding: 6em 9em 2em 0; }
#logo {
  h1 {
    display: inline-block;
    margin-bottom: 0.20em;
    padding: 0.20em 0.9em;
    font-size: 3.5em; }
  a {
    text-decoration: none;
    color: hsla(0, 0%, 100%, 1); } }
#menu ul {
  border-bottom: none; }
#menu li a, #menu li span {
  display: inline-block;
  padding: 1.3em 1.5em;
  text-decoration: none;
  font-size: 0.90em;
  text-transform: uppercase;
  outline: 0;
  color: hsla(0, 0%, 100%, 1); }

.index {
  #menu li:hover a, #menu li:active a, #menu li.active span {
    background: hsla(222, 42%, 42%, 1); } }

.main {
  padding: 60px; }

section {
  .divn {
    font-size: 16px;
    color: hsla(0, 0%, 20%, 1);
    border-bottom : 2px solid hsla(0, 0%, 73%, 1);
    margin-bottom: 30px;
    padding-bottom: 10px;
    .number {
      font-family: 'NotoKR-bd';
      font-size: 14px;
      color: hsla(0, 0%, 73%, 1);
      margin-right: 5px; } } }

section {
  .title {
    font-family: 'NotoKR-md';
    font-size: 16px;
    color: hsla(0, 0%, 73%, 1); } }

.main .column-1 {
  float: left;
  width: 50%; }

.flex {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  > * {
    flex: 1; } }
