@import "../_variable";

#logo h1 {
  padding-top: 70px;
  a {
    font-size: 1em;
    padding-top: 10px; } }
section {
  margin-bottom: 65px; }

section.list {
  border-top: none; }

p a {
  text-align: left; }

.basic_style {
  p {
    display: inline-block;
    margin-right: 20px; } }


.basic_style {
  .boldy {
    font-family: 'NotoKR-bd';
    font-weight: bold; }
  .italicy {
    font-style: italic; }
  .hypery {
    color: #039be5; } }

// BUTTONS
.buttons .btn_group a {
  display: inline-block;
  margin: {
    right: 10px;
    bottom: 18px; } }

.buttons .btn_list {
  display: block;
  .btn_normal, p {
    display: inline-block; }
  .btn_normal {
    margin-bottom: 10px;
    line-height: 125%;
    span {
      display: block; }
    &:nth-child(2) {
      color: #999; }
    &.btn_lg {
      line-height: 150%; }
    &.btn_md {
      line-height: 150%; } }
  p {
    color: #999;
    margin-left: 10px; } }

.buttons .btn_group.on_group {
  .ex_hover {
    background-color: #4fc3f7;
    color: $white;
    box-shadow: 0px 2px 8px lighten($text-color, 30%); }
  .ex_active {
    background-color: #039be5;
    border: 2px solid transparent;
    color: $white; } }

.buttons .btn_group {
  .ex_hover {
    background-color: #78909c;
    color: $white;
    box-shadow: 0px 2px 8px lighten($text-color, 30%);
    border: 2px solid transparent; }
  .ex_active {
    background-color: #455a64;
    color: $white; } }

ol {
  padding-left: 15px; }

.list .list_guide {
  margin-top: 15px;
  .list {
    margin-right: 50px; } }

section.list > ul li, section.list > ol li {
  border: none;
  padding: 0; }

section.list > ul, section.list > ol {
  border-bottom: none; }

section.list .divn {
  margin-top: 0px; }

section.list div.divn {
  padding-bottom: 65px; }

.form {
  input, select, textarea, pre {
    margin-bottom: 20px; } }

.table_area {
  .table {
    width: 420px;
    th {
      &:first-child {
        width: 60px; }
      &:nth-child(2) {
        width: 230px; }
      &:nth-child(3) {
        width: 125px; } }
    td {
      &:first-child {
        text-align: center; }
      &:nth-child(2) {
        padding-left: 15px; }
      &:nth-child(3) {
        text-align: center; } } } }

.colors {
  .color_list {
    margin-left: 10px;
    margin-right: 10px;
    &:first-child {
      margin-left: 10px; } } }

.colors p.divn {
  margin-bottom: 62px; }

@mixin color_box {
  display: inline-block;
  width: 80px;
  height: 45px;
  &:nth-child(2) {
    margin-left: -5px; } }

.colors .color_list {
  display: inline-block;
  border: 3px solid #eee;
  .color_txt {
    padding-left: 10px;
    span.color_name {
      display: block;
      font-size: 12px;
      color: $text-color; }
    span.color_val {
      display: block;
      font-size: 12px {
        color: #999; } } }
  &.dark_gray {
    .color_box {
      @include color_box;
      &:first-child {
        background-color: $text-color; }
      &:nth-child(2) {
        background-color: #666;
        margin-left: -5px; } } } }

.colors .color_list {
  &.light_gray {
    .color_box {
      @include color_box;
      &:first-child {
        background-color: #f9f9f9; }
      &:nth-child(2) {
        background-color: #dddddd; } } } }

.colors .color_list.blue {
  .color_box {
    @include color_box;
    &:first-child {
      background-color: #039be5; }
    &:nth-child(2) {
      background-color: #4fc3f7; } } }

.colors .color_list.blue_gray {
  .color_box {
    @include color_box;
    &:first-child {
      background-color: #455a64; }
    &:nth-child(2) {
      background-color: #78909c; } } }

.icon .ic_deco {
  display: inline-block; }

.icon_list span {
  display: inline-block;
  vertical-align: middle;
  margin-right: 30px; }

.icon_list .logout span {
  display: block;
  font-size: 16px;
  color: #333; }

.icon_list .logout .txt {
  font-size: 12px;
  color: #686871; }

.icon_list div {
  margin-right: 50px;
  font-size: 22px; }

.icon_list .ic_star {
  margin-right: 15px; }

.carousel {
  width: 350px;
  height: 150px; }
.carousel ul {
  border-bottom: none; }
